@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: Arial, sans-serif;
  background: #f4f4f4;
  color: #222;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 900px;
  margin: 40px auto;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px #0001;
  padding: 32px;
}

h1, h2, h3 {
  color: #2c3e50;
}

input, button, select {
  font-size: 1rem;
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid #bbb;
  margin-bottom: 12px;
}

button {
  background: #3498db;
  color: #fff;
  border: none;
  cursor: pointer;
  margin-right: 8px;
}
button:hover {
  background: #217dbb;
}

.card {
  background: #fafafa;
  border: 1px solid #eee;
  border-radius: 6px;
  padding: 20px;
  margin-bottom: 20px;
}

.flex {
  display: flex;
  gap: 16px;
}

.flex-col {
  flex-direction: column;
}

.text-center {
  text-align: center;
}

.mt-2 { margin-top: 16px; }
.mb-2 { margin-bottom: 16px; }
.mt-4 { margin-top: 32px; }
.mb-4 { margin-bottom: 32px; } 